import { data } from "pages/news/data";
import { Item } from "./Item";
import { ArrowRight } from "pages/main/sections/faq/ArrowRight";
import { useState } from "react";

export const Layout = () => {
    const [currentPage, setCurrentPage] = useState<number>(1);
    const postPerRow = 3;
    const posts = data.slice(0, currentPage * postPerRow);
    const isAllShown = data.length === posts.length;

    const handleClick = () => {
        setCurrentPage((currentValue) => currentValue + 1);
    };

    return (
        <section className="section">
            <div className="wide-container mt-10 mobile:mt-0 flex-col">
                <h2 className="title mb-6">
                    Insights to Guide Your Financial Journey
                </h2>
                <h4 className="under-title mt-0 mobile:-mt-2 mb-0">
                    Read our expert tips, guides, and updates on financial
                    solutions
                </h4>
                <div
                    className="grid mobile:block w-full"
                    style={{
                        gridTemplateColumns:
                            "repeat(auto-fit, minmax(337px, 1fr))",
                        gap: "1.5rem",
                    }}
                >
                    {posts.map((post) => (
                        <Item key={post.slug} post={post} />
                    ))}
                </div>
                {!isAllShown && (
                    <button
                        type="button"
                        className="btn_primary w-[192px] mt-10 ml-auto"
                        onClick={handleClick}
                    >
                        <span>View more</span>
                        <ArrowRight />
                    </button>
                )}
            </div>
        </section>
    );
};
