import React from "react";
import { useIsMobile } from "hooks/use-is-mobile";
import { AppDecor } from "./AppDecor";
import { AppDecorXl } from "./AppDecorXl";
import { AppDecorSm } from "./AppDecorSm";
import { AppDecorMd } from "./AppDecorMd";
import { AppDecorLg } from "./AppDecorLg";
import styles from "./styles.module.scss";

type DecorSizeType = "sm" | "md" | "lg" | "xl";

const AppDecorMobile: React.FC<{ size: DecorSizeType }> = ({ size }) => {
    switch (size) {
        case "sm":
            return <AppDecorSm />;
        case "md":
            return <AppDecorMd />;
        case "lg":
            return <AppDecorLg />;
        case "xl":
            return <AppDecorXl />;
    }
};

export const Layout: React.FC<{
    id: string;
    size: DecorSizeType;
    children: React.ReactNode;
}> = ({ id, size, children }) => {
    const isMobile = useIsMobile();
    return (
        <section id={id} className="section -mt-appAdvertIndent relative">
            {isMobile ? <AppDecorMobile size={size} /> : <AppDecor />}
            <div className="wide-container pt-0 mobile:px-0">
                <div className={styles.bannerData}>{children}</div>
            </div>
        </section>
    );
};
